import { createApp } from 'vue';
import $ from 'jquery';
import _ from 'lodash';
import router from './';
import app from './app.vue';
// import '@fortawesome/fontawesome-free/css/all.min.css';
// import 'toastr/build/toastr.min.css';

//  APP_READY undefined
try {
  (window as any).APP_READY = false;
} catch {
  // ignore
}
try {
  (window.parent as any).APP_READY = false;
} catch {
  // ignore
}
try {
  (window.top as any).APP_READY = false;
} catch {
  // ignore
}

//  jQuery/ $
(window as any).$ = $;
(window as any).jQuery = $;

//  lodash/ _
(window as any)._ = _;

// Vue Router$(() => {})
const vueApp = createApp(app);
vueApp.use(router);

import { logger, perfLogger, mvuLogger, sysLogger } from './logger';

// 
const perfStartTime = performance.now();

// Web Vitals
const initWebVitalsMonitoring = () => {
  try {
    // LCP (Largest Contentful Paint)
    const lcpObserver = new PerformanceObserver(entryList => {
      const entries = entryList.getEntries();
      const lastEntry = entries[entries.length - 1];
      perfLogger.perf(`LCP: ${lastEntry.startTime.toFixed(2)}ms`, lastEntry);
    });
    lcpObserver.observe({ type: 'largest-contentful-paint', buffered: true });

    // FID (First Input Delay) - Event Timing API
    const fidObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (entry.entryType === 'first-input') {
          const firstInput = entry as PerformanceEventTiming;
          const fid = firstInput.processingStart - firstInput.startTime;
          perfLogger.perf(`FID: ${fid.toFixed(2)}ms`, firstInput);
        }
      }
    });
    fidObserver.observe({ type: 'first-input', buffered: true });

    // CLS (Cumulative Layout Shift)
    let clsValue = 0;
    const clsEntries: LayoutShift[] = [];
    const clsObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (entry.entryType === 'layout-shift') {
          const layoutShift = entry as LayoutShift;
          if (!layoutShift.hadRecentInput) {
            clsEntries.push(layoutShift);
            clsValue += layoutShift.value;
            perfLogger.perf(`CLS: ${clsValue.toFixed(4)}`, layoutShift);
          }
        }
      }
    });
    clsObserver.observe({ type: 'layout-shift', buffered: true });

    //  (Long Tasks)
    const longTaskObserver = new PerformanceObserver(entryList => {
      for (const entry of entryList.getEntries()) {
        if (entry.duration > 50) {
          // 50ms
          perfLogger.perf(`: ${entry.duration.toFixed(2)}ms`, entry);
        }
      }
    });
    longTaskObserver.observe({ type: 'longtask', buffered: true });

    perfLogger.log('Web Vitals');
  } catch (error) {
    perfLogger.warn('Web Vitals:', error);
  }
};

// Chrome
const initMemoryMonitoring = () => {
  try {
    // performance.memory API
    if ('memory' in performance) {
      const memory = (performance as any).memory;
      perfLogger.perf(
        `: ${(memory.usedJSHeapSize / 1024 / 1024).toFixed(2)}MB / ${(memory.totalJSHeapSize / 1024 / 1024).toFixed(2)}MB`,
      );

      // 
      setInterval(() => {
        const mem = (performance as any).memory;
        const usedMB = mem.usedJSHeapSize / 1024 / 1024;
        const totalMB = mem.totalJSHeapSize / 1024 / 1024;
        const percentage = (usedMB / totalMB) * 100;
        const percentageText = percentage.toFixed(1);

        // 
        if (usedMB > 50 || percentage > 80) {
          perfLogger.warn(`: ${usedMB.toFixed(2)}MB/${totalMB.toFixed(2)}MB (${percentageText}%)`);
        }
      }, 30000); // 30

      perfLogger.log('');
    } else {
      perfLogger.log('performance.memory API');
    }
  } catch (error) {
    perfLogger.warn(':', error);
  }
};

// 
const preloadCriticalRoutes = async () => {
  try {
    // 
    const criticalRoutes = ['/service', '/play'];
    await Promise.all(
      criticalRoutes.map(path => {
        const route = router.resolve(path);
        // Vue Router
        return route;
      }),
    );
    perfLogger.log('');
  } catch (error) {
    perfLogger.warn(':', error);
  }
};

// 
const preloadCriticalResources = async () => {
  try {
    // 
    const resources: string[] = [
      // 
    ];

    if (resources.length === 0) return;

    await Promise.all(
      resources.map(url => {
        return new Promise((resolve, reject) => {
          const link = document.createElement('link');
          link.rel = 'prefetch';
          link.href = url;
          link.onload = resolve;
          link.onerror = reject;
          document.head.appendChild(link);
        });
      }),
    );
    perfLogger.log('');
  } catch (error) {
    perfLogger.warn(':', error);
  }
};

// MVU
const waitForMvu = async (timeout = 5000) => {
  //  waitGlobalInitialized
  if (typeof (window as any).waitGlobalInitialized !== 'function') {
    mvuLogger.info(' MVU ');
    return null;
  }

  return Promise.race([
    waitGlobalInitialized('Mvu'),
    new Promise((_, reject) => setTimeout(() => reject(new Error('MVU')), timeout)),
  ]).catch(err => {
    mvuLogger.warn(':', err);
    return null; // null
  });
};

// 
const initApp = async () => {
  try {
    // 
    const initTasks = Promise.all([
      waitForMvu(), // MVU
      preloadCriticalRoutes(), // 
      preloadCriticalResources(), // 
    ]);

    // 
    const [mvuReady] = await initTasks;

    //  iframe localStorage.setItem('app-perf-monitor','1')
    const enablePerfMonitoring = (() => {
      try {
        return localStorage.getItem('app-perf-monitor') === '1';
      } catch {
        return false;
      }
    })();
    if (enablePerfMonitoring) {
      initWebVitalsMonitoring();
      initMemoryMonitoring();
    }

    // 
    const initTime = performance.now() - perfStartTime;
    perfLogger.log(`: ${initTime.toFixed(2)}ms`);

    // MVU
    if (mvuReady) {
      sysLogger.log(`(${initTime.toFixed(0)}ms)`);
    } else {
      sysLogger.warn(' (MVU)');
    }

    // Vue
    vueApp.mount('#app');

    //  APP /
    try {
      (window as any).APP_READY = true;
    } catch {
      // ignore
    }
    try {
      (window.parent as any).APP_READY = true;
    } catch {
      // ignore
    }
    try {
      (window.top as any).APP_READY = true;
    } catch {
      // ignore
    }
    try {
      if (typeof (window as any).eventEmit === 'function' && typeof (window as any).tavern_events !== 'undefined') {
        // APP_READY
        (window as any).eventEmit((window as any).tavern_events.APP_READY);
      }
    } catch {
      // ignore
    }

    // 
    const mountTime = performance.now() - perfStartTime;
    perfLogger.log(`: ${mountTime.toFixed(2)}ms`);
  } catch (err) {
    sysLogger.error(':', err);
    // 
  }
};

//  jQuery  DOMContentLoaded
$(() => {
  //  - VueonMounted
  initApp();
});

//  jQuery 
$(window).on('pagehide', () => {
  const unloadTime = performance.now() - perfStartTime;
  perfLogger.log(`: ${unloadTime.toFixed(2)}ms`);
  sysLogger.log('');
});

//  - Promise
window.addEventListener('unhandledrejection', event => {
  sysLogger.error('Promise:', event.reason);
  try {
    window.dispatchEvent(
      new CustomEvent('app-fatal-error', {
        detail: { type: 'unhandledrejection', reason: event.reason },
      }),
    );
  } catch {
    // ignore
  }
});

//  - JavaScript
window.addEventListener('error', event => {
  sysLogger.error('JavaScript:', event.error);
  try {
    window.dispatchEvent(
      new CustomEvent('app-fatal-error', {
        detail: {
          type: 'error',
          error: event.error,
          message: event.message,
          filename: event.filename,
          lineno: event.lineno,
        },
      }),
    );
  } catch {
    // ignore
  }
});
